



import React, { useState, useEffect } from 'react';

// src/main-interface/Header.js
import "./Header.css";
import llllllllllllllggggggggg from '../images/llllllllllllllggggggggg.png';
import Button from '@mui/material/Button';
import HomeIcon from '@mui/icons-material/Home';
import { Link } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';

const contentButtons = (handleMenuClick) => (
  <div className="content-button">
    <Link to="/" style={{ textDecoration: 'none' }}>
      <Button className='home-page' variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/doctors">
      <Button variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/consultation">
      <Button variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/articles">
      <Button variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/booking">
      <Button variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/newsFeed">
      <Button variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/login" style={{ textDecoration: 'none' }}>
      <Button className='bttn-log-reg' variant="contained" onClick={handleMenuClick}> </Button>
    </Link>
    <Link to="/register" style={{ textDecoration: 'none' }}>
      <Button className='bttn-log-reg' variant="contained" onClick={handleMenuClick}></Button>
    </Link>
  </div>
);

const Header = () => {
  const [showButtons, setShowButtons] = useState(false);

  const handleMenuClick = () => {
    setShowButtons(!showButtons);
  };

  useEffect(() => {
    if (showButtons) {
      //      
      document.body.style.overflow = 'hidden';
    } else {
      //      
      document.body.style.overflow = 'auto';
    }

    //     
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [showButtons]);

  return (
    <>
      <div className='all-header'>
        <header className="header">
          {/* Logo */}
          <div className="logo">
            <img src={llllllllllllllggggggggg} alt="Logo" />
          </div>

          {/* Search Box */}
          <div className="search-box">
            <input
              style={{ textAlign: "center" }}
              type="text"
              placeholder="search"
            />
          </div>

          {/* Buttons */}
          <div className="buttons">
            <Link to="/login">
              <Button> </Button>
            </Link>
            <Link to="/register">
              <Button></Button>
            </Link>
          </div>

          {/* Home Icon */}
          <div className='icon-home'>
            <Link to="/">
              <HomeIcon style={{ fontSize: '60px', color: "#003366" }} />
            </Link>
          </div>
        </header>

        {/* MENU ICON */}
        <div>
          <MenuIcon className='menue-icon' onClick={handleMenuClick} />
        </div>

        <div className="father-button">
          {contentButtons(handleMenuClick)}
        </div>
      </div>

      {/* Dropdown menu */}
      <div>
        {showButtons && <div className='drop-down'>{contentButtons(handleMenuClick)}</div>}
      </div>
    </>
  );
}

export default Header;

